import React from "react";
import "../styles/header.scss";
import Budge from "./Budge";

export default function Header({ backetClickHandler, count, onKeyUpHandler}) {
    return (
        <div id="header">
            <div className="container">
                <div className="header-box">
                    <div className="image-box">
                        <img src={require('../sourse-image/shop-logo.png')} alt="logo icon shop n save" className="logo"/>
                    </div>
                    <div className="wrapper">
                        <div className="search-box">
                            <input type="text" name="search" id="searchInp" onKeyUp={(e) => onKeyUpHandler(e.target.value)} placeholder="Я хочу.." className="input"/>
                            <button className="btn">Найти</button>
                        </div>
                        <div className="cart-box" onClick={() => backetClickHandler()}>
                            {count !== 0 && <Budge count={count} />}
                            <img src={require('../sourse-image/cart-icon.png')} alt="cart icon" className="cart-icon"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}