import '../styles/viewer.scss';
import React from 'react';
import ProductView from './ProductView';
import Title from './Title';

export default function Viewer(props) {
    
    function prodClick(data){
        props.callbacker(data);
    }
    
    const prod = props.data.map(item => {
        return <ProductView clickHandler={prodClick} data={item} key={item.id}/>
    });

    return (
        <div className="box-viewer">
            {props.category !== null && <Title text={props.category} />}
            <div className="viewer">
                {prod}
            </div>
        </div>
    )
}