import React, {useEffect, useState} from 'react';
import './App.css';
import Header from "./components/Header";
import $ from 'jquery';
import Viewer from './components/Viewer';
import Sidebar from './components/Sidebar';
import Basket from './components/Basket';
import './styles/main.scss';
// import Calendar from './components/Сalendar';


function App() {
    const [products, setProducts] = useState([]);
    const [viewProducts, setviewProducts] = useState(null);
    const [selectedCategory, setSelectedCategory] = useState('Главная');
    const [categories, setcategories] = useState([]);
    const [cart, setCart] = useState([]);
    const [isCartOpen, setisCartOpen] = useState(false);
    
    useEffect(() => {
        
        $.ajax({
            type: "get",
            url: "./server/?prod=all",
            success: (response) => {
                const data = JSON.parse(response);
                setProducts(data);
                setviewProducts(data);
            },
            error: (request) => {
                console.log(request.readyState);
            }
        });
        $.get("./server/?categories=all", response => {
            setcategories(JSON.parse(response));
        });
    }, []);

    function filtrationHandler(id) {
        if (id === 'all') {
            setviewProducts(products);
            setSelectedCategory('Главная');
        } else {
            setviewProducts(products.filter(product => product.category.id === id));
            const currentCategory = categories.filter( category => category.id === id)[0];
            setSelectedCategory(currentCategory.name);
        }
    }

    function addToCart(product) {
        if (!cart.includes(product)) {
            const newStateCart = cart.slice(0);
            newStateCart.push(product);
            setCart(newStateCart);    
        }
    }

    function openCart() {
        !isCartOpen ? setisCartOpen(true) : setisCartOpen(false);
    }

    function search(text) {
        setviewProducts(products.filter(product => product.model.toLowerCase().includes(text)));
    }

	return (
        <div className="App">
            <header>
                <Header backetClickHandler={openCart} count={cart.length} onKeyUpHandler={search}/>
                <Basket data={cart} state={isCartOpen} changeState={openCart} />
            </header>
            <main>
                    <Sidebar
                        categories={categories}
                        filtrationHandler={filtrationHandler}
                    />
                
                    {viewProducts !== null ? (
                        <Viewer
                            data={viewProducts}
                            category={selectedCategory}
                            callbacker={addToCart}
                        />
                    ) : <h4 style={{'marginLeft': '600px', 'marginTop': '300px', 'color': 'rgba(0,0,0, .5)'}}>Загрузка...</h4>}
                
            </main>
        </div>
    );
}

export default App;
