import React, {useState} from 'react';
import '../styles/small-product-view.scss';

export default function SmallProductView({data}) {
    const [count, setCount] = useState(1);
    
    function incClickHandler(){
        const newCount = count+1;
        setCount(newCount);
    }

    return(
        <div className="prod-box">
            <div className="wrapper-helper">
                <div className="count-box">
                    {count}
                </div>
                <div className="image">
                    <img src={data.image} alt={data.name}/>
                </div>
            </div>
            <div className="title-box">
                <div className="title">
                    {data.name + " " + data.model}
                </div>
                <div className="sub-title">
                    Цена: {data.price} грн.
                </div>
            </div>
            <div className="increment-btn" onClick={incClickHandler}>
                <img src={require('../sourse-image/plus-ico.png')} alt="increment btn icon"/>
            </div>
        </div>
    )
}