import React from 'react';
import '../styles/sidebar.scss';
import FilterBadge from './FilterBadge';
import HomeButton from './HomeButton';

export default class Sidebar extends React.Component{
    constructor(props) {
        super(props);
        this.state = {
            selectedCategory: 'all',
        };
    }

    filterClickHandler = (id) => {
        this.setState({
            selectedCategory: id
        });

        this.props.filtrationHandler(id);
    }

    homeButtonClickHandler = () => {
        this.filterClickHandler('all');
    }

    render(){
        const selectedCategory = this.state.selectedCategory;
        const categories = this.props.categories;
        const elements = categories.map(
            category => <FilterBadge data={category} key={category.id} clickHandler={this.filterClickHandler} isActive={false} selected={selectedCategory}/> );

        return (
            <div className="sidebar">
                <div className="sidebar__categories" style={{'width': '100%'}}>
                    <h2 className="cat_title">Каталог</h2>
                    {selectedCategory !== 'all' && (<div className="sidebar__img-box">
                                                        <HomeButton clickHandler={this.homeButtonClickHandler} />
                                                    </div>)}
                    {elements}
                </div>
            </div>
        )
    }
}

