import '../styles/basket.scss';
import React from 'react';
import SmallProductView from './SmallProductView';
import FullPriceCounter from './FullPriceCounter';

export default function Basket ({data, state, changeState}) {
    function closeClickHandler() {
        changeState();
    }
    
    const elements = data.map( item => <SmallProductView key={item.id} data={item} />);
    return (
        <div className="basket" style={!state ? {top: '-100%'} : {display: '0'}}>
            <h2 style={{'color': 'rgba(255, 255, 255, .9)', 'margin': '0'}}>Корзина</h2>
            {elements.length > 0 ? elements : <h4 style={{'color': 'rgba(255, 255, 255, .9)'}}>Тут пусто(</h4>}
            {elements.length > 0 ? (<div className="full-price">
            <FullPriceCounter priceArray={data.map(item => item.price)}/>
            </div>) : ''}
            <div className="btn" onClick={closeClickHandler}>
                <img width="30" src={require('../sourse-image/up-arrow-ico.png')} alt="up button icon"/>
            </div>
        </div>
    )

}