import '../styles/prod-view.scss';

import React from 'react';

export default function ProductView({data, clickHandler}) {
    return (
        <div className="card">
            <div className="card-body">
                <div className="card-body__img-box">
                    <img className="card-body__img" src={ data.image } alt={ data.name + " " + data.model }/>
                </div>
                <div className="card-body__title-box">
                    {data.name + " " + data.model}
                </div>
                <div className="card-body__price-box">
                    {data.price} грн
                </div>
                <div className="card-body__btn-box">
                    <button className="btn">Посмотреть</button>
                    <button className="btn" onClick={() => clickHandler(data)}>Купить</button>
                </div>
            </div>
        </div>
    )
}