function FilterBadge (props){
    
    let styles = props.data.id !== props.selected ? { 'backgroundColor': 'rgba(41, 67, 78, .5)' } : { 'backgroundColor': '#d32f2f' };
        return (
            <div className="filter-badge" style={styles} onClick={(e) => {
                props.clickHandler(props.data.id);
            }}>
                {props.data.name}
            </div>
        )
    
}

export default FilterBadge;